import {useState} from 'react'

import './App.css';
import figlet from 'figlet';
import standard from 'figlet/importable-fonts/Colossal.js'// const FONT_OPTS = {
import small from 'figlet/importable-fonts/Small.js'// const FONT_OPTS = {

import useEventListener from './useEventListener'
  

figlet.parseFont('Standard', standard);
figlet.parseFont('Small', small);

function gapMStoBPM (ms) {
  const sec = ms / 1000
  
  return 60 / sec
}



function Display({bpm}) {
  const text = figlet.textSync(bpm, {
    font: 'Standard',
  });

  return <pre className='bpm'>{text}</pre>
}

function App() {
  const [taps, setTaps] = useState([])

  function handler({ key }) {
    if (key === ' ') {
      setTaps([...taps, Date.now()])
    }
  }

  let bpm = 'TAP...'
  if (taps.length > 1) {
    const deltas = [];
    
    for (let i = 1; i < taps.length; i++) {
      let d = taps[i] - taps[i-1];
      deltas.push(d)
    }
    
    const adder = (acc, currentValue) => acc + currentValue;
    const avgGapMS = deltas.reduce(adder) / deltas.length;
    const avgBPM = gapMStoBPM(avgGapMS);


    bpm = Math.round(avgBPM)

  }

  useEventListener('keydown', handler)

  return (
    <div>

      <Display bpm={bpm}></Display>
    </div>
  );
}

export default App;
